/*=========================================================== 1. ELEMENTS ====*/


section {
	margin-top: 2em;
}

table {
	border-collapse: collapse;

	td {
		padding: 0.1em 0;

		&:nth-child(1n+2) {
			padding-left: 2em;
		}
	}
}


/*============================================================= 2. BLOCKS ====*/
/*------------------------------------------------------------- 2.1 DASHTILES */


.dashTiles {
	display: grid;
	grid-auto-rows: 10em;
	grid-auto-columns: 10em;
	grid-auto-flow: column;
	grid-gap: 10px;
	margin-top: 2em;

	a {
		display: flex;
		align-items: center;
		justify-content: center;
		transition: all .15s;
		border-radius: 5px;
		box-shadow: 0px 0px 20px 0px #0003;
		background-color: var(--lw_white);
		padding: 10px;
		text-align: center;
		text-decoration: none;
		color: var(--lw_grey-dark);
		font-size: 120%;
		font-weight: 500;

		&:hover {
			box-shadow: 0px 0px 20px 0px #0008;
			text-decoration: underline;
			color: var(--lw_grey-darkest);
		}
	}
}


/*---------------------------------------------------- 2.2 DASHICONS DROPDOWN */


.dashiconsDropdown {
	width: auto;

	a {
		display: inline-block;
		overflow: hidden;
		text-decoration: none;
		border: 1px solid #8c8f94;
		font-size: 14px;
		line-height: 2;
		color: #2c3338;
		box-shadow: none;
		border-radius: 3px;
		padding: 0 48px 0 8px;
		min-height: 30px;
		max-width: 25rem;
		background: #fff url(data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%2220%22%20height%3D%2220%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20d%3D%22M5%206l5%205%205-5%202%201-7%207-7-7%202-1z%22%20fill%3D%22%23555%22%2F%3E%3C%2Fsvg%3E) no-repeat right 5px top 55%;
		background-size: 16px 16px;
		cursor: pointer;
		vertical-align: middle;

		span {
			vertical-align: inherit;
		}
		
		&.open {
			color: #000;
		}
	}
	
	a.open + ul {
		display: block;
	}
	
	ul {
		list-style-type: none;
		padding: 5px;
		margin: 0;
		display: none;
		border: 1px solid #000;
		
		li {
			display: inline-block;
			
			input {
				display: none;
			}
			
			label {
				display: inline-block;
				padding: 4px;
				cursor: pointer;
				color: #777;
				border: 1px solid transparent;
				
				&:hover {
					color: #000;
				}
			}
			
			:checked + label {
				color: #000;
				border-color: #000;
			}
		}
	}
	
	.dashiconsDropdown_removeButton {
		width: 100%;
		
		button {
			display: block;
			font-size: 13px;
			border: none;
			padding: 5px;
			background: transparent;
			color: #b32d2e;

			&:hover {
				cursor: pointer;
				text-decoration: underline;
			}
		}
	}
}

.visuallyhidden {
	display: none;
}